@charset "UTF-8";
:root {
  --foreground-color: #ffffff;
  --background-color: #121922;
}

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Fira Sans"), url("/assets/fonts/FiraSans-Regular.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Fira Sans Light"), url("/assets/fonts/FiraSans-Light.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Fira Sans Bold"), url("/assets/fonts/FiraSans-Bold.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 900;
  font-display: swap;
  src: local("Fira Sans Black"), url("/assets/fonts/FiraSans-Black.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
@font-face {
  font-family: "Fira Code";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Fira Code"), url("/assets/fonts/FiraCode.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
@font-face {
  font-family: "Merriweather";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Merriweather"), url("/assets/fonts/Merriweather-Regular.ttf") format("truetype");
  unicode-range: U+000-5FF; /* Latin glyphs */
}
.button {
  display: inline-block;
  appearance: none;
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 3px;
  border-width: 0;
  border-style: solid;
  color: var(--background-color);
  background-color: var(--foreground-color);
  font-family: var(--gh-font-body, "Fira Sans", -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Droid Sans, Helvetica Neue, sans-serif);
  font-weight: 600;
  font-size: 0.83rem;
  margin: 2px 0;
}
.button.primary {
  background-color: var(--ghost-accent-color);
  color: var(--background-color);
}
.button.secondary {
  background-color: #5682F2;
  color: white;
}
.button.highlighted {
  background-color: #5682F2;
  background: linear-gradient(85deg, var(--ghost-accent-color) 0%, #5682F2 80.21%);
  background-size: 200% 200%;
  animation: highlightButton 3s ease alternate infinite;
  color: white;
}
.button.rounded {
  border-radius: 50%;
  padding: 10px 10px;
}
.button:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

@keyframes highlightButton {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
